import axios from 'axios';

export const API_BASE_URL = 'http://127.0.0.1:8000';

export const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// API endpoints
export const translatePrompt = async (prompt: string) =>{
    const response = await api.post('/translate', { prompt });
    return response.data;
};

export const validateParams = async (params: any) =>{
    const response =  await api.post('/validate', params);
    return response.data;
}

export const renderImage = async (params: any) =>{
    const response = await api.post('/render', params);
    return response.data;
}

export const uploadControlNet = async (file: File , imageType: string) =>{
    const formData = new FormData();
    formData.append('file', file);
    formData.append('image_type', imageType);
    const response = await api.post('/upload_controlnet', formData, {
        headers: {
            'Content-Type': 'multipart/form-data',
        },
    });
    return response.data;
}

export const getVersions = async () =>{
    const response = await api.get('/versions');
    return response.data;
}